@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hello{
    width: 100%;
    height: 32rem;
    background-color: rgb(31, 30, 30);
    color: white;
    padding: 1rem;
    font-family: 'Ubuntu', sans-serif;
   
   
}
.hello p{
    font-size: 4rem;
    margin-left: 1rem;
    margin-top:  9%;
}
.like{
    font-weight:0px;
}
.name{
    font-weight: bold;
    font-size: 4rem;
    
}
.my-name{
    font-size: 2.5rem;
    margin-left: 4.5rem;
}
.text{
    color: rgb(167, 17, 167);
    font-style: italic;
    font-weight:500;
}

@media (max-width:576px) {
    .hello{
        width: 100%;
        height: 24rem;
        background-color: rgb(31, 30, 30);
        color: white;
        padding: 1rem;
        font-family: 'Ubuntu', sans-serif;
        
    }
    .hello p{
        font-size: 2.2rem;
        margin-left: 1rem;
        margin-top:  20%;
    }
    .name{
        font-weight: 500;
        font-size: 2.2rem;
    }
    .my-name{
        font-size: 1.8rem;
        margin-left: 2rem;
    }
    .text{
        color: rgb(167, 17, 167);
        font-style: italic;
        font-weight:500;
        font-size: 2rem;
    }
    .d{
        position: relative;
    }
}
.cv{
    text-align: center;
}
.card{
    border: 0px;
    
}
.card-img-top{
   width: 18rem;
   height: 18rem;
   margin: auto;
   margin-top: 5rem;
   border-radius: 100%;
   background-color:  rgb(31, 30, 30);
}
.card-text{
    text-align: center;
}
.title{
    margin: auto;
    margin-top: 4rem;
    margin-bottom: 2rem;
    text-align: center;
    color: rgb(167, 17, 167);
   
}
.about-content{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    padding: 1rem;
}

.text1{
    margin:1rem;
}
.icons{
    display: -webkit-flex;
    display: flex;
    font-size: 2rem;
}
.icons a{
    padding: 1rem;
    margin: auto;
    color:rgb(31, 30, 30);
}
.btn-cv{
    background-color:rgb(167, 17, 167) ;
    color: white;
    margin: auto;
}
@media (max-width:768px) {
    .about{
        margin-bottom: 1rem;
    }
}
@media (max-width:576px) {
    .about-content{
        display: block;
    }
   
    .title{
        margin: auto;
        margin: 0 2rem;
        padding-bottom: 1rem;
        text-align: center;
        color: rgb(167, 17, 167);
    }
    


    .card-img-top{
        width: 14rem;
        height: 14rem;
    }
    .title{
      padding-top: 2rem;
       
    }
}


.contacto{
    margin:auto;
    width: 100%;
    
}
.send{
    text-align: center;
    
}
.btn-send{
    background-color:rgb(167, 17, 167) ;
    color: white;
    margin: auto;
    width: 7rem;
    
}
.text-work{
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
   
}

.input-text{
    margin: 2rem 6rem;
}

.menu{   
    background-color: rgb(167, 17, 167);

}
.navbar-toggler{
    font-size: .8rem;
    padding-left: 1rem;
}
.navbar{
    padding-left: 1rem;
}
.navbar-nav{
    padding-right: 1rem;
}
.card-img-project{
   
    margin: auto;
    background-color:  rgb(31, 30, 30);
    object-fit: cover;
    width: 325px;
    height: 181px;
    max-width: 100%;
}
.card-p{
    width: 18rem;
    box-shadow: 2px 2px 10px rgb(167, 17, 167);
    margin-left: auto;
    margin-right: auto;
}
.card-body-p{
    margin: 0.5rem;
}
.pro{
    -webkit-align-items: center;
            align-items: center;
}
.projects{
    color: white;
    background-color: rgb(31, 30, 30);
    padding-bottom: 1rem;
}
.title-p{
    padding: 1rem;
    text-align: center;
    color: rgb(167, 17, 167);
}
.tech{
    color: white;
    padding: 1rem;

}
.tecnologies{
    font-size: 4rem;
    background-color: rgb(31, 30, 30);
    padding: 2rem;
    margin: auto;
}

.tecno{
    text-align: center;
}
@media (max-width:576px) {
    .tecnologies{
        font-size: 2rem;
    }
   
}
